export const RedirectPage: React.FC = () => {
  const location = useLocation();
  const {
    site: {
      siteMetadata: { siteUrl },
    },
  } = useStaticQuery<Query>(
    graphql`
      query {
        site {
          siteMetadata {
            siteUrl
          }
        }
      }
    `,
  );

  const query = qs.parse(location.search);

  const catid = query['catid'];
  const artid = query['artid'];

  const article = articles.find((v) => v.id === artid);
  const category = categories.find((v) => v.id === catid);

  const slug = article?.slug ?? category?.slug ?? '';

  const redirect = `${siteUrl}/${slug}`;

  const content = `0; url=${redirect}`;

  return (
    <html>
      <head>
        <meta httpEquiv="refresh" content={content}></meta>
      </head>
      <body>
        <script>
          setTimeout(window.location.href="{redirect}", 2000);
        </script>
        This page has a new <a href={redirect}>location</a>.
      </body>
    </html>
  );
};

export default RedirectPage;
